html,
body {
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  background-color: var(--lightShade);
  margin: 0;
  font-family: montserrat !important;
}

h1 {
  color: var(--mainBrand) !important;
}

p {
  color: var(--darkShade) !important;
}

a {
  color: var(--darkShade) !important;
}

a:hover {
  color: var(--mainBrand) !important;
}

/* Navbar Styling */

#navOpen {
  display: block;
  z-index: 1;
  position: fixed;
  right: 1rem;
  top: 1rem;
}

#navOpen Link {
  /* Replace a with proper react-scroll link component tag */
  text-decoration: none;
  color: #232323;
  transition: color 0.3s ease;
}

#navOpen:hover span {
  background-color: var(--mainBrand);
}

#navOpen:hover input:checked~span {
  /* What is ~ ???? */
  background-color: var(--mainBrand);
}

#navOpen input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;

  right: 1rem;
  top: 1rem;

  cursor: pointer;
  opacity: 0;
  /* hide this */
  z-index: 2;
  /* and place it over the hamburger */
  -webkit-touch-callout: none;
}

#navOpen input:checked {
  position: absolute;
  top: 0px;
  left: -96vw;
  width: 100vw;
  height: 100vh;
}

#navOpen span {
  display: block;
  width: 2rem;
  height: .2rem;
  margin: .3rem;
  border-radius: 3px;
  background-color: var(--lightAccent);
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0),
    opacity 0.7s ease;
}

#navOpen span:first-child {
  transform-origin: 0% 0%;
}

#navOpen span:nth-child(4) {
  transform-origin: 0% 100%;
}

#navOpen input:checked~span {
  transform: rotate(45deg) translate(-2px, -1px);
  background: var(--lightAccent);
}

#navOpen input:checked~span:nth-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}

#navOpen input:checked~span:nth-child(4) {
  transform: rotate(-45deg) translate(0, -1px);
}

#menuBg {}


/* Add another div that contains menu and the background blur,
replace menu with the new div in other rules,  */
#menu {
  position: absolute;
  z-index: -1;
  width: 100vw;
  margin: -100px 0 0 -95vw;
  padding: 50px;
  padding-top: 20vh;
  /* Area above slideover menu links */
  padding-bottom: 70vh;
  /* Area under slideover menu links */
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  background-color: rgba(0, 0, 0, 0.61);
  backdrop-filter: blur(5px);
  transform-origin: 0% 0%;
  transform: translate(100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);
}

#navLinks {
  position: absolute;
  left: 45vw;
  z-index: 3;
  width: fit-content;

}

#menu li {
  color: var(--darkAccent);
  padding-bottom: 3%;
  font-size: 22px;
}

#menu li:hover {
  color: var(--mainBrand);
}

/*
 * And let's slide it in from the left
 */
#navOpen input:checked~ul {
  transform: none;
}

.sectionHeader {
  padding-left: 15px;
}

#contentSection {
  transition: 1s;
}


#hero-grid {
  /*height:  50%;*/
  display: grid;
  grid-template-rows: 0.5fr 1fr 1fr 0.5fr;
  grid-template-columns: 1fr 1.5fr 1.5fr 2fr 1fr;
  background-color: var(--lightShade);
  height: 100vh;
  text-overflow: clip;

  grid-template-areas:
    ". . . . ."
    ". titleWrap titleWrap . ."
    ". . . infoWrap infoWrap "
    ". . . . .";
}



.titleWrap {
  grid-area: titleWrap;
}

.title {
  color: var(--mainBrand) !important;
  font-family: montserrat !important;
  font-size: 5em;
}

.infoWrap {
  color: var(--lightAccent);
  grid-area: infoWrap;
  font-size: 1.5em;
}



.about-section {
  display: grid;
  background: var(--lightAccent);
}

#aboutMePicWrap {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 3%;
  height: 100%;
  margin-bottom: 15px;
}

#aboutMePic {
  border-radius: 3% !important;
  max-width: 100%;
  max-height: 370px;
}


.chip {
  display: inline-block;
  padding: 0 15px;
  margin: 1%;
  height: 50px;
  font-size: 16px;
  line-height: 50px;
  border-radius: 25px;
  background-color: var(--lightShade);
}

.box {
  padding: 10px;
  margin-right: 30px
}



.project-section {
  background: var(--lightShade);
}

.project-wrap {
  display: flex;
  justify-content: center;
  /* Not working?? Project cards are off center on narrow screens */
}

.project {
  text-align: center;
  display: inline-block;
  color: var(--darkShade);
}

#gitHubCard h5 {
  font-weight: 1000;
}

.project-card {
  background: var(--lightAccent);
  /* Eventually add custom colors for each project */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin-left: 1%;
  margin-right: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  max-width: 82vw !important;
  min-height: 175px !important;
  border-radius: 10px;
  margin-bottom: 20px;
}

.project-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.9);
}

.gitHubCard {
  margin-left: 6%;
  text-align: center;
  padding-top: 2%;
  background: var(--lightAccent);
  /* Eventually add custom colors for each project */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 10px;
  margin-top: 80px;
  display: grid;
}

.gitHubCard:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.9);
}

#githubimg {
  display: inline-block;
  vertical-align: center;
  max-width: 50%;
}

.blocked {
  /* Overlay Coming Soon msg and darken background */
}

.gallery-section {
  display: grid;
  background: var(--lightAccent);
  padding-bottom: 1vw;
}

.ReactGridGallery_tile-viewport img,
.ReactGridGallery_tile img {
  border-radius: 15px !important;
}

.contact-section {
  display: grid;
  background: var(--lightShade);
}

#formWrap {
  filter: blur(15px);
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 2fr 1fr 2fr 1fr;
}

#nameWrap {
  grid-column: 2;
  grid-row: 1;
}

#emailWrap {
  grid-column: 2;
  grid-row: 2;
}

#messageWrap {
  grid-column: 4;
  grid-row: 1 2;
}


/*Add smooth scroll functionality later via jQuery button listener */
div.arrow {
  width: 6vmin;
  height: 6vmin;
  box-sizing: border-box;
  position: absolute;
  left: 48.5%;
  top: 90%;
  transform: rotate(135deg);
}

div.arrow::before {
  content: '';
  width: 100%;
  height: 100%;
  border-width: .8vmin .8vmin 0 0;
  border-style: solid;
  border-color: var(--lightAccent);
  transition: .1s ease;
  display: block;
  transform-origin: 100% 0;
}

div.arrow:after {
  content: '';
  float: left;
  position: relative;
  top: -100%;
  width: 100%;
  height: 100%;
  border-width: 0 .8vmin 0 0;
  border-style: solid;
  border-color: var(--lightAccent);
  transform-origin: 100% 0;
  transition: .2s ease;
}

div.arrow:hover::after {
  transform: rotate(45deg) translateX(.4vmin) translateY(.4vmin);
  border-color: var(--mainBrand);
  height: 110%;
}

div.arrow:hover::before {
  border-color: var(--mainBrand);
  transform: scale(.8);
}

#alertBar {
  background: var(--darkShade);
  color: var(--lightShade);
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  border-radius: 0px;
}

.close {
  background-color: var(--darkShade);
  float: right;
  opacity: 1;
  padding-top: .2rem !important;

}

@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--darkShade);
  }

  p {
    color: var(--darkShade);
  }


  #navOpen {
    color: var(--lightShade);
  }

  .sidenav a {
    color: var(--lightShade);
  }

  .title {
    color: var(--mainBrand);
  }

  #hero-grid {
    background-color: var(--darkShade);
  }

  .infoWrap {
    color: var(--darkAccent);
  }

  .info {
    color: var(--darkAccent) !important;
  }

  #alertBar {
    background: var(--darkAccent);
  }

  #aboutMePicWrap {
    filter: brightness(85%);

  }

  #techOverview {
    padding-right: 5px;
  }

  .chip {
    background-color: var(--darkShade);
    color: var(--lightShade);
  }

  div.arrow::before {
    border-color: var(--darkAccent);
  }

  div.arrow:after {
    border-color: var(--darkAccent);
  }

  .project-section {
    background: var(--darkShade);
  }

  .project-card {
    /* Eventually custom for each project */
    background: var(--darkAccent);
  }

  .gitHubCard {
    background: var(--darkAccent);
  }

  .about-section {
    background: var(--darkAccent);
  }

  .contact-section {
    background: var(--darkAccent);
  }
}

@media only screen and (max-width: 585px) {

  #menu {
    margin: -20vw 0 0 -87vw;
  }

  #menu li {
    padding: 10vw 44vw 0 35%;
  }

  #hero-grid {
    grid-template-areas:
      ". . . . ."
      ". titleWrap titleWrap . ."
      ". . infoWrap infoWrap ."
      ". . . . .";
  }


  #introFacts {
    padding-left: 5%;
  }

  div.arrow:hover::after {
    transform: rotate(45deg) translateX(2px) translateY(4px);
  }

  aboutMePic {
    margin-left: 20vw;
  }

  .project-section {
    flex-direction: column;
  }

  .project-card {
    max-width: 90%;
  }

  .gitHubCard {
    margin-left: 5%;
    max-width: 90%;
    margin-top: 0px;
    margin-bottom: 3%;

  }

  #githubimg {
    max-width: 20%;
  }
}


@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}